.project-wrapper {
  margin-top: 25px;
  background-color: #fff;
  border-radius: 10px;
}

.project-wrapper h3, .project-wrapper h5, .project-wrapper span, .project-wrapper p, .project-wrapper li {
  color: #111;
}

.project-heading {
  text-align: center;
}

.project-heading h3 {
  margin-top: 10px;
}

.rails-project h5 {
  font-weight: bold;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .project-wrapper h5 {
    font-size: 85%;
    margin-top: 0;
    margin-bottom: 0;
  }
  .project-wrapper ul {
    padding-left: 20px;
  }
  .project-wrapper p, .project-wrapper li {
    font-size: 60%;
  }
  .rails-project a h5 {
    text-decoration: underline;
  }
}