.professional-wrapper {
  margin-top: 25px;
  background-color: #fff;
  border-radius: 10px;
}

.professional-wrapper h3, .professional-wrapper h5, .professional-wrapper p, .professional-wrapper span, .professional-wrapper li {
  color: #111;
}

.professional-wrapper h5 {
  font-style: italic;
}

.professional-heading {
  text-align: center;
}

.professional-heading h3 {
  margin-top: 10px;
}

.caret-icon {
  display: inline-block;
  height: 20px;
  width: 20px;
  transform: translateY(-3px);
  cursor: pointer;
}

.contact-icon-col {
  text-align: center;
}

.contact-icon {
  height: 70px;
  width: 70px;
}

.h5-careted {
  display: inline-block;
}

@media only screen and (max-width: 768px) {
  .professional-heading h3 {
    font-size: 125%;
  }
  .identity h5, .qualities h5 {
    font-size: 85%;
    margin-top: 0;
    margin-bottom: 0;
  }
  .identity span {
    font-size: 75%;
  }
  .identity ul, .qualities ul {
    padding-left: 20px;
  }
  .identity li, .identity p, .qualities li {
    font-size: 60%;
  }
  .caret-icon {
    height: 10px;
    width: 10px;
    transform: translateY(-1px);
  }
  .contact-icon-text {
    font-size: 60%;
  }
  .contact-email-text {
    font-size: 70%;
  }
  .contact-icon {
    height: 30px;
    width: 30px;
  }
}